@import '~node_modules/modern-normalize/modern-normalize.css';
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
body {
  scroll-behavior: smooth;
}

html {
  font-size: 16px;
}
@media screen and (min-width: 768px) {
  html {
    font-size: 24px;
  }
}
@media screen and (min-width: 1280px) {
  html {
    font-size: 32px;
  }
}

body {
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: 0.04em;
  color: #ffffff;
  text-align: center;
  box-sizing: border-box;
}
body:not(input) {
  user-select: none;
}

.is-hidden {
  overflow: hidden;
}

.hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.container {
  margin: 0 auto;
  font-size: 0rem;
  position: relative;
}
@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

img {
  display: block;
}

.home {
  font-size: 0;
}

.home-container,
.library-container {
  height: 230px;
  padding-top: 42px;
  padding-left: 20px;
  padding-right: 20px;
}
@media screen and (min-width: 768px) {
  .home-container,
  .library-container {
    height: 216px;
  }
}

.home-container {
  background-image: url("../images/head-home-320.png");
}
@media screen and (min-width: 768px) {
  .home-container {
    background-image: url("../images/head-home-768.png");
  }
}
@media screen and (min-width: 1280px) {
  .home-container {
    background-image: url("../images/head-home-1280.png");
  }
}

.library-container {
  background-image: url("../images/head-library-320.png");
}
@media screen and (min-width: 768px) {
  .library-container {
    background-image: url("../images/head-library-768.png");
  }
}
@media screen and (min-width: 1280px) {
  .library-container {
    background-image: url("../images/head-library-1280.png");
  }
}

.library {
  display: flex;
  justify-content: center;
  margin-top: 62px;
  font-size: 0.875rem;
  line-height: 114%;
}
@media screen and (min-width: 768px) {
  .library {
    margin-top: 40px;
  }
}

.library-label {
  border: solid 1px #ffffff;
  padding: 14px 36px;
  border-radius: 5px;
  position: relative;
  cursor: pointer;
}
.library-label:not(:last-child) {
  margin-right: 16px;
}

input[type=radio]:checked + label {
  background: #ff6b08;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.text-logo {
  font-size: 0;
}
@media screen and (min-width: 768px) {
  .text-logo {
    font-size: 30px;
    color: #ffffff;
    margin-left: 10px;
  }
}

.nav {
  display: flex;
}

.now-here {
  position: relative;
}
.now-here::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0;
  height: 2px;
  width: 100%;
  background: #ff001b;
}

.nav-list:not(:last-child) {
  margin-right: 40px;
}
.nav-list a {
  color: #ffffff;
  text-decoration: none;
  font-size: 20px;
  display: block;
}

.search {
  display: block;
  margin-top: 56px;
}
@media screen and (min-width: 768px) {
  .search {
    margin-top: 40px;
  }
}

.search-label {
  position: relative;
  text-align: center;
}

.search-input {
  font-size: 14px;
  color: #ffffff;
  background: none;
  border: none;
  outline: none;
  width: 280px;
  margin-bottom: 16px;
  padding-bottom: 4px;
  border-bottom: 1px solid #ffffff;
}
@media screen and (min-width: 768px) {
  .search-input {
    width: 336px;
    margin-bottom: 20px;
  }
}

.search-svg {
  position: absolute;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 3px 4px;
  right: 0;
  top: 50%;
  transform: translateY(-80%);
}

.search-error {
  font-size: 0.14px;
  color: #ff001b;
  text-align: left;
}
@media screen and (min-width: 768px) {
  .search-error {
    text-align: center;
  }
}

.list-films {
  display: grid;
  grid-template: 1fr 1fr/1fr;
  justify-items: center;
  row-gap: 20px;
  padding: 20px 20px 0;
}
@media screen and (min-width: 768px) {
  .list-films {
    grid-template: 1fr/1fr 1fr 1fr;
    column-gap: 20px;
    justify-items: start;
    padding: 20px 0 0;
  }
}
.list-films img {
  width: 100%;
  height: auto;
}
.list-films .films {
  cursor: pointer;
  width: 100%;
  text-align: left;
}
.list-films h2 {
  font-size: 0.625rem;
  line-height: 133%;
  color: #000000;
  padding-top: 10px;
}
@media screen and (min-width: 768px) {
  .list-films h2 {
    padding-top: 0.35rem;
  }
}
.list-films .second-title {
  padding-top: 3px;
}
.list-films p {
  color: #ff6b01;
}

.pagination {
  padding: 60px 0;
  display: flex;
  justify-content: center;
}
.pagination .page-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .pagination {
    padding: 40px 0;
  }
}
.pagination button,
.pagination li {
  margin: 0;
  border: none;
  border-radius: 5px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.pagination button:active,
.pagination li:active {
  transform: scale(0.9);
}
.pagination .prev,
.pagination .next {
  background: #f7f7f7;
}
.pagination .page {
  font-size: 12px;
  background: transparent;
  color: #000000;
}
.pagination .page.active {
  background: #ff6b08;
}
.pagination .page:hover {
  background: #ff6b08;
}

footer {
  font-size: 14px;
  line-height: 1.1;
  color: #545454;
  padding: 30px 0;
  background: #f7f7f7;
}
footer p:not(:last-child) {
  padding-bottom: 10px;
}
footer svg {
  vertical-align: middle;
  margin: 0 10px;
}
@media screen and (min-width: 768px) {
  footer {
    display: flex;
    justify-content: center;
  }
}

.backdrop {
  position: fixed;
  padding: 50px 0 0;
  z-index: 5;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3137254902);
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  transition: opacity 500ms cubic-bezier(0.17, 0.67, 0.92, 0.51);
}
.backdrop.is-open {
  pointer-events: auto;
  opacity: 1;
  transition: opacity 500ms cubic-bezier(0.17, 0.67, 0.92, 0.51);
}

.top {
  transform: translate(-50%, -50%) rotate(45deg);
}

.bottom {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.modal {
  background: #ffffff;
  position: relative;
  width: 320px;
  padding: 48px 20px 40px;
  border-radius: 5px;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .modal {
    width: 704px;
    display: flex;
    justify-content: space-between;
  }
}

.card-img {
  border-radius: 5px;
  width: 100%;
  height: auto;
}

.close {
  position: absolute;
  background: transparent;
  border: none;
  cursor: pointer;
  top: 16px;
  right: 16px;
  width: 14px;
  height: 14px;
}
.close span {
  display: block;
  width: 10px;
  height: 2px;
  border-radius: 2px;
  background: #000000;
  position: absolute;
  top: 50%;
  left: 50%;
  transform-origin: 50% 50%;
}

.card-film {
  color: #000000;
}
@media screen and (min-width: 768px) {
  .card-film {
    width: 704px;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 1fr;
    column-gap: 20px;
    padding: 0 20px;
  }
}

.fistful {
  font-size: 12px;
  line-height: 1.1;
}

.title-film {
  font-size: 20px;
  text-transform: uppercase;
  padding: 20px 0;
  text-align: left;
}

.firste-title {
  display: flex;
  justify-content: space-between;
  color: #8c8c8c;
  font-weight: 500;
  line-height: 1.8;
}
.firste-title:not(:last-child) {
  padding-bottom: 8px;
}

.vote {
  background: #ff6b01;
  color: #ffffff;
  border-radius: 5px;
  padding: 1px 9px;
}

.detail {
  color: #000000;
  width: 50%;
  text-align: left;
}

.about-film {
  margin: 20px 0;
  text-align: left;
}

.about-text {
  margin-top: 8px;
}

.add-film {
  display: flex;
  justify-content: center;
}

.add-film button {
  cursor: pointer;
  padding: 14px 9px;
  border-radius: 5px;
}
.add-film button:active {
  transform: scale(0.8);
}
.add-film button:disabled {
  transform: scale(1);
  background: #f0f0f0;
}

.add-wotched {
  color: #ffffff;
  background: #ff6b01;
  border: none;
  margin-right: 15px;
}

.add-queue {
  background: transparent;
}

